@import '../../styles';

.detailed {

  &Container {
    @include containerDesktop;
  }

  &Card {
    max-width: 80%;
    display: flex;
    gap: 30px;
    margin: 30px auto 30px auto;
    padding: 25px;
    background-color: var(--bgc-main);
    //box-shadow: var(--shadow-btn);
    box-shadow: 0 2px 7px rgba(0, 0, 0, .23);
    border-radius: 15px;
  }

  &Img {
    width: 400px;
    height: 400px;
    border-radius: 5px;
  }

  &WrapperInfo {
    & span {
      @include text;
      font-weight: 600;
      margin-right: 10px;

    }
  }

  & ul {
    display: flex;
  }
}
